
import {getAllInfo, getSubMenu, getSearch} from '@/api/address-book';
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'addressBook',
components: {Scroller, Cell, Group},
data() {
return {
firstList: true,//
subList: false,//
people: true,//
isChecked: false,
showContent: true,//
isSmall: false,//
cancle: false,//
searchText: "",//
branchList: [],//
allInfoList: "",//
subInfoList: "",//
peopleList: "",//
choseId: "",//id
isSelf: false,//
selfName: "",//,
nullTips: false,//
showResult: false,//
searchResult: "",
}
},
methods: {
searchOne(){//
this.showContent = false;
this.isSmall = true;
this.cancle = true;
this.getSearchList();
this.showResult = true;
},
cancleQuit(){//
this.showContent = true;
this.isSmall = false;
this.cancle = false;
this.showResult = false;
},
goPrevious(item, index){//
console.log(item);
if (item.id === 1) {
this.firstList = true;
this.subList = false;
this.getAllList();
this.branchList.splice(-1, this.branchList.length - index - 1);
} else {
this.subList = true;
this.getSubList(item.id || item.departmentId);
this.branchList.splice(-1, this.branchList.length - index - 1);
}
this.isSelf = true;
},
itemClick(item){
this.branchList.push(item);
this.selfName = item.name;
this.isChecked = true;
this.choseId = item.id;
this.firstList = false;
this.subList = true;
this.getSubList(this.choseId);
},
goDetail(item){
this.$router.push({
name: 'memberDetail',
params: {"id": item.userId}
});
},
getAllList(){//
getAllInfo({},).then(response => {
if (response.data && response.data.rows) {
this.allInfoList = response.data.rows[0].deptInfo;
if (this.branchList.length === 0) {
this.branchList.push({
id: response.data.rows[0].deptInfo[0].id,
name: response.data.rows[0].deptInfo[0].name
});//
}
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
getSubList(param){//
getSubMenu({id: param},).then(response => {
if (response.data && response.data.rows) {
this.subInfoList = response.data.rows[0].childrenDept;
if (response.data.rows[0].childrenDept === null) {
this.firstList = false;
this.subList = false;
this.people = false;
this.nullTips = true;
}
this.peopleList = response.data.rows[0].deptStaff;
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
getSearchList(){
getSearch({"key": this.searchText, "page": "1", "pagesize": "15"},).then(response => {
if (response.data && response.data.rows) {
this.searchResult = response.data.rows;
console.log("", this.searchResult);
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
},
mounted() {
this.getAllList();
this.getSearchList();
},
created() {
},
}



